import { useDisclosure } from '@chakra-ui/hooks'
import { Drawer, DrawerBody, DrawerContent, DrawerOverlay } from '@chakra-ui/modal'
import React, { lazy, Suspense, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { updateCollectionViewOpen, updateSelectedCollectionId } from './state/collection/reducer'
import { Box } from '@chakra-ui/react'

const Collection = lazy(() => import('./widgets/Collection/collection'))

function App() {
  const { selectedCollectionId, showMint, collectionViewOpen } = useSelector((state) => state.collection)
  const accessKey = useSelector((state) => state.user.accessKey)
  const dispatch = useDispatch()
  const { isOpen, onOpen, onClose } = useDisclosure()
  const closeDrawer = () => {
    // onClose()
    dispatch(updateCollectionViewOpen({ collectionViewOpen: false }))
    dispatch(updateSelectedCollectionId({ id: null }))
  }

  // useEffect(() => {}, [collectionViewOpen, selectedCollectionId])

  // useEffect(() => {
  //   if (selectedCollectionId && !showMint && !collectionViewOpen) {
  //     dispatch(updateCollectionViewOpen({ collectionViewOpen: true }))
  //     // onOpen()
  //   }
  // }, [selectedCollectionId, collectionViewOpen])

  // if (!selectedCollectionId) return null
  return (
    <Suspense fallback={null}>
      <Drawer
        placement={'bottom'}
        onClose={() => dispatch(updateCollectionViewOpen({ collectionViewOpen: false }))}
        isOpen={collectionViewOpen}
        closeOnOverlayClick={false}
      >
        <DrawerOverlay bg="rgba(0,0,0,0.8)" />
        <DrawerContent h="93%" bg="transparent">
          <DrawerBody p="0" borderTopRightRadius={'20'} borderTopLeftRadius={'20'} bg="background">
            <Box height="100%">
              <Collection accessKey={accessKey} collectionId={selectedCollectionId} closeDrawer={closeDrawer} />
            </Box>
          </DrawerBody>
        </DrawerContent>
      </Drawer>
    </Suspense>
  )
}

export default App
